import { ConvertToPluginActionCreators } from "@ali/4ever-cangjie";

export const MARK_TOGGLE = Symbol('MARK_TOGGLE');

export function toggleMark(mark) {
  return {
    type: MARK_TOGGLE,
    payload: {
      mark,
    },
  };
}

export const actions = {
  toggleMark,
}

declare module "@ali/4ever-cangjie" {
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions> {}
}
